.Covid19 {
  text-align: center;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  min-height: 100vh;

  &__Header {
    margin: 1em;
    font-size: 2.5em;
    letter-spacing: 0.4em;
    color: #2a363b;
  }

  &__GlobalStatistics {
    display: flex;
    justify-content: center;
    margin: 0 auto;
  }

  @media screen and (max-width: 500px) {
    &__GlobalStatistics {
      flex-direction: column !important;
      margin-left: 2em;
      margin-right: 2em;
    }
  }

  @media screen and (max-width: 1500px) {
    &__GlobalStatistics {
      flex-direction: row;
    }
  }

  &__SearchForm {
    margin-top: 1em;
  }

  &__CountryAreaChart {
    display: flex;
    justify-content: center;
    margin-top: 1em;
    margin-bottom: 4em;
  }

  &__Footer {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: auto;

    &--h3 {
      max-width: 50%;
    }

    &--h4 {
      margin: 0;
      max-width: 50%;
    }

    &--p {
      max-width: 40%;
    }
  }
}
